import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";

import Toast from "../toast";

import { toast } from "../../utils/toastService";
import { getPosition } from "../../helpers";
import { StyledToaster } from "./styled";

function Toaster({ position = "bottom-left" }) {
  const [toasts, setToasts] = useState([]);

  const positionProp = getPosition(position);
  const elem = document.getElementById("modal");

  toast.bindSetToasts(setToasts);

  useEffect(() => {
    const timer = setInterval(() => {
      if (toasts?.length > 0) {
        toast.removeToast(toasts[toasts?.length - 1].id);
      }
    }, 3000);

    return () => clearInterval(timer);
  }, [toasts]);

  return ReactDOM.createPortal(
    <StyledToaster position={positionProp}>
      {toasts?.map((item) => (
        <Toast
          key={item.id}
          id={item.id}
          variant={item.variant}
          message={item.message}
          position={positionProp}
        />
      ))}
    </StyledToaster>,
    elem,
  );
}


export default Toaster;
